<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-actions>
          <v-text-field
            label="Lösungswort:"
            :success="answered"
            clearable
            :rules="[validate]"
          ></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from 'vue';

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Republika",
    unterkapitel1: 'Die Überfahrt zum größten Hafen der Welt war ruhig. Die riesige Tordurchfahrt in das Hafenbecken war mit Symboliken der vier Elemente geschmückt. Hunderte Staturen von Bändigern waren am Fuß der Hafenmauer aufgereiht und streckten ihre Hände gen Horizont.',
    unterkapitel2: 'Als ich das Schiff verlassen hatte, fühlte sich alles wieder an wie vor der Reise. Am Hafen von Republika stand ich schon oft und träumte davon mal etwas mehr von der Welt zu sehen. Doch nie hatte ich einen Grund mein Leben zu verlassen. Immer wieder verschob ich meine Wünsche aus den Zwängen, die mir mein Leben auferlegte. Doch nun stand ich hier und nichts war mehr wie zuvor. Ich hatte es einfach getan. Ich war gegangen aus Neugier und Abenteuerlust. Nun bin ich so nah dran. Das holte Walters Tagebuch raus. Es war die einzige Quelle, die mir jetzt noch helfen konnte. Schon oft hatte Walter die richtigen Orte besucht, hatte aber nie etwas hilfreiches geschrieben. Auf den letzten Seiten schrieb er von einer kleinen Teestube im Seidendistrikt der Stadt und lobte den Tee und die Gesellschaft. Ich machte mich auf den Weg zu der beschriebenen Teestube. Gespannt zog ich durch Gassen voller Menschen, Trubel und Gerüchen von Unzähligen Teriyakiständen. Ich betrat die Teestube und guckte mich um. Es war sehr schön hier doch nichts wirkte besonders. Über der Eingangstür hing ein Gemälde, das einen Mann mit Avatar Fentimon zeigte. "Darf ich ihnen einen Tee machen, der Herr?" Fragte mich ein Bediensteter alter Mann mit einer kleinen Verbeugung. "Ja bitte. Etwas Entspannung wird mir gut tun." Antwortete ich und erwiderte die Verbeugung. Ich wurde zu einem runden Tisch geführt, um den 4 Stühle standen. Ein Pai Sho Spielfeld war in der Tischplatte eingearbeitet. Ich bestellte einen Tee, legte Walters Tagebuch auf den Tisch und lehnte mich zurück. Eine Weile verging und meine Teetasse wurde sorgfältig auf dem Tisch platziert und aufgegossen. Doch es war nicht der alte Mann der mich begrüßt hatte. Walter stand neben mir. Als der Tee aufgegossen war, verbeugte er sich und setzt sich mir gegenüber. Er stellte seine Teetasse vor sich und zog einen Kasten mit Pai Sho-Steinen aus der Tischplatte hervor. "Kannst du Spielen?" Fragte er. Ich antwortete "Mein Vater hat es mich gelehrt." "Gut. Dann verrate mir eins. Durch welche Bändigungsart starben im letzten Jahr die meisten bekannten Pferde?'
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "RVJEQsRORElHRU4=") {
        this.answered = true;
      } else {
        this.answered = false;
      }
      
      return this.answered
    }
  }
};
</script>

<style>
