<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
           <v-card-actions>
          <v-text-field label="Lösungswort:" :success="answered" clearable :rules="[validate]"></v-text-field>
        </v-card-actions>
        <v-card-text v-if="answered">{{ unterkapitel3 }}</v-card-text>
        <v-card-actions v-if="answered">
          <v-text-field label="Lösungswort:" :success="answered2" clearable :rules="[validate2]"></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered2" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from "vue";

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    answered2: false,
    headline: "Astrales",
    unterkapitel1:
      "Ich berührte die Symbole auf der Tür in der Reihenfolge. Sie leuchteten kurz lila auf und ein Zischen war zu hören. Die Tür öffnete sich und dahinter war eine alte Schriftrolle. Als ich sie öffnete erkannte ich, dass es eine Karte war. Sie war alt und verschnörkelt geschrieben. Die Rückseite war komplett vollgeschrieben. Wenn das eine Beschreibung für den Weg war, wollte ich sie jetzt schon nicht lesen. “Da hätte sich der Autor echt mal kürzer fassen können.” Sagte ich zu Kero, der grinsend neben mir stand. Er antwortete spaßend: “Hier ist ein X auf der Karte. Geh doch einfach da hin. Schade. Ich dachte wir finden den Schal schon hier. Hopp Hopp. Mach dich auf den Weg.”",
    unterkapitel2:
      'In diesem ruhigen Land des leise flüsternden Windes, tief in den Dschungel gebaut, war eine der größten Städte der Welt. Astrales war umgeben von einer riesigen Mauer, doch die Natur machte daran nicht Halt. Man hörte Affen schreien, Vögel balzen, Geparden brüllen und Blätter rascheln. Die Gebäude waren riesig und sahen zu Teilen selbst aus wie die umgebenen Bäume. Hier musste ich Rast machen. Die Karte führte mich in den tiefsten Dschungel. Das Dickicht mit seinen giftigen Tieren und Pflanzen sollte man nicht unvorbereitet betreten. Zu viele Abenteurer haben schon nach den Schätzen alter Zivilisationen gesucht und sind nie zurückgekehrt. Ich betrat das Gasthaus, das einem Prunkvollen Waldanwesen glich und wurde sogleich vom Gastwirt begrüßt, der gerade sein Runde an den voll besetzten Tischen machte. Mit offenen Armen kam er auf mich zu. "Willkommen fremder Freund. Nenn mich Dreifan. Mir gehört dieses bescheidene Gasthaus. Wie kann ich dich glücklich stimmen?” "Ich brauche eine Machete, Proviant und ein Zimmer zum Schlafen.” "Gut. Gut. Ein Abenteurer.” Er sagte mir wo ich Essen und Ausrüstung bekam und gab mir ein sehr schönes Zimmer. Ich Zeigte ihm noch meine Karte und er gab mir einen Hinweis. Dort wo das X auf der Karte verzeichnet war, soll es viele Höhlen geben. Mit diesem Wissen fühlte ich mich gut gewappnet und legte mich schlafen.',
    unterkapitel3:
      'Die Nacht war ruhig. Ich fühlte mich nach dem Schlaf wie neu geboren. Die Geräusche der Tiere und die kühle Nachtluft haben mich schon am Abend zuvor total entspannt. Also Packte ich zusammen und machte ich mich auf durch die Stadttore. Ich verließ den befestigten Weg, rein ins dichte Unterholz, wo mich nur noch Karte und Kompass leiten konnten.'
      }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "TUFDSEVURQ==") {
        this.answered = true;
      } else {
        this.answered = false;
      }

      return this.answered;
    },
    validate2: function(input) {
      if (input == undefined) {
        this.answered2 = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "QkxBVFRMQVVTTU9TS0lUTw==") {
        this.answered2 = true;
      } else {
        this.answered2 = false;
      }

      return this.answered2;
    }
  }
};
</script>

<style>
</style>
