<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>
          <v-layout align-center column>
            <v-list>
              <v-list-tile style="height:35px" v-for="r in raetsel" :key="r">
                <i class="body-1">{{ r }}</i>
              </v-list-tile>
            </v-list>
          </v-layout>
        </v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-actions>
          <v-text-field label="Lösungswort:" :success="answered" clearable :rules="[validate]"></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from "vue";

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Luftgeformte Küste",
    unterkapitel1:
      'An der Küste angekommen holte ich meine Bücher vor. In Walters Tagebuch stand kein Wort darüber, in welcher Stadt er sein Abenteuer bei den Luftnormanden begann. Er schrieb über die Luftbändiger und deren gütige Gastfreundschaft. Die Aufschriften auf Türen schienen ihm eine Erwähnung wert gewesen zu sein. Bloß wo er diese Türen fand schrieb er nicht. "Das ist doch alles nichts.” Sagte ich zu mir und schlug das Buch der 4 Relikte auf. Was suchte ich überhaupt? Es ging um das Luftbändigen, um Chakra, Ying und Yang, um die Entstehung der Lufttempel. Und wieder gab es ein Rätsel.',
    raetsel: [
      "Hinter Siegeln versteckt.",
      "Schwere Tür in schwerelosen Räumen entdeckt.",
      "Über Fluss und Tal schwebt ein Schatz versiegelt.",
      "Luftgewebter Stoff glitzert und spiegelt.",
      "Trag mich um den Hals, denn der Weg ist weit.",
      "Ich trag dich um die Welt, nimm dir die Zeit."
    ],
    unterkapitel2:
      "Wo sollte ich nach schwerelosen Räumen suchen? Ying und Yang? Das habe ich doch schon mal gesehen. Es war auf einem Bild in einem Reiseführer riesengroß in einer Brücke. Welche Stadt war das nur?"
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "WUFNQSBOTyBLQVpF" || btoa(input) == "WUFNQV9OT19LQVp") {
        this.answered = true;
      } else {
        this.answered = false;
      }

      return this.answered;
    }
  }
};
</script>

<style>
</style>
