<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>
          <v-layout align-center column>
            <v-list>
              <v-list-tile style="height:35px" v-for="r in raetsel" :key="r">
                <i class="body-1">{{ r }}</i>
              </v-list-tile>
            </v-list>
          </v-layout>
        </v-card-text>
        <v-card-actions>
          <v-text-field label="Lösungswort:" :success="answered" clearable :rules="[validate]"></v-text-field>
        </v-card-actions>
        <v-card-text v-if="answered">{{ unterkapitel2 }}</v-card-text>
        <v-card-text v-if="answered">{{ unterkapitel3 }}</v-card-text>
            <v-card-actions v-if="answered">
          <v-text-field label="Lösungswort:" :success="answered2" clearable :rules="[validate2]"></v-text-field>
        </v-card-actions>
        <v-card-text v-if="answered2">{{ unterkapitel4 }}</v-card-text>
        <v-card-actions>
          <v-btn :disabled="!answered2" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from "vue";

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    answered2: false,
    headline: "Der Schal der Wolken",
    unterkapitel1:
      "Auf der Wand der Höhle waren Inschriften. Mist. Wieder nur ein weiterer Hinweis. Ich war ermüdet von dem ganzen Machetenschwüngen und den Sprinteinlagen, zu denen mich die Blattlausmoskitoschwärme gebracht hatten. Nun musste ich erstmal ausruhen und verstehen wohin mich das Rätsel führte. Ich setzte mich am Eingang der Höhle auf einen Baumstumpf und sinnierte über das die Inschriften.",
    raetsel: [
      "Dort wo sonst nur Luft gewesen,",
      "steht ein Turm im Wolkenkleid.",
      "Was du suchst liegt dort in Ehren,",
      "doch du brauchst ne Menge Schneid.",
      "Die Balance zu halten fordert Geschick,",
      "versteckt hab ichs auf schmaler Planke.",
      "Fallen bricht dir dein Genick,",
      "doch Erfolg dich befreit von jeder Schranke."
    ],
    unterkapitel2:
      "Mein weg führte mich durch den großen Tunnel unter der See, der auf der Welt berühmt war für seinen Blick unter den Ozean. Als Wasserbändiger war das kein ungewohnter Anblick, da ich in meiner Kindheit jeden Tag getaucht bin. Jedoch war es auch mal schön sich trocken durchs Wasser bewegen zu können. Am Ende des Tunnels genoss ich die frische Luft. Ich reckte den Kopf hoch und sah, wieso das Rätsel von einem Wolkenkleid sprach. Der Turm war so hoch, dass die kleinen Dunstwolken an diesem Sommertag, wie ein Kleid um seine Mitte trieben. Hier würde ich das Relikt des ersten Luftbändigers finden! Ich ging zum offenen Eingangstor, wo ich Wachen erwartet hätte. Jedoch stand mir die Treppe ohne Kontrolle offen. Die Menschen schoben sich an mir vorbei, weil ich etwas unentschlossen Halt machte. Ich ging ein Stück zur Seite und blieb neben einem Schild an der Wand stehen. Darauf fanden sich einige Angaben zu Wolkenstadt. Die Familie Tamoon baute einst diesen Koloss und betreibt ihn bis heute. Dazu fand ich eine Angabe über die Anzahl der Stufen, die man wohl am bequemsten mit dem Luftroller hochkommt. Nur dass ich kein Luftbändiger bin. Ich machte mich mit schmerzenden Füßen auf den Weg nach oben.",
    unterkapitel3:
      "Licht durchflutete Säle und ein Blick direkt ins Blau des Himmels. Die Stadt war Atemberaubend. Nun muss ich nur noch das Relikt finden. Dann haben sich die Stufen auch gelohnt.",
    unterkapitel4:
      "Ich hatte ihn gefunden! Der Schal der Wolken sah nach einem ganz normalen weißen Schal aus. Man fühlte jedoch, dass er aus Luftbisonfell gewebt war. Ich legte ihn um und fühlte mich sofort leichter. Sehr viel leichter. War es Einbildung? Ich stieg ein paar Treppen hoch und machte einen weiten Satz wieder hinunter. Das Ergebnis war trotz allem unerwartet. Ich schwebte so weit, dass ich mich von der 4 Meter entfernt liegenden Wand wieder abstoßen musste und erst nach 2 weiteren Metern wieder sanft den Boden berührte. Was würde wohl passieren, wenn ich direkt aus Wolkenstadt springen würde? Wie weit würde ich wohl kommen? Nein. Das traute ich mich noch nicht. Ich nahm leichtfüßig die Treppe nach unten."
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "V09MS0VOU1RBRFQ=") {
        this.answered = true;
      } else {
        this.answered = false;
      }

      return this.answered;
    },
    validate2: function(input) {
      if (input == undefined) {
        this.answered2 = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "U1BSVU5HIElOUyBCTEFVRQ==") {
        this.answered2 = true;
      } else {
        this.answered2 = false;
      }

      return this.answered2;
    }
  }
};
</script>

<style>
</style>
