<template>
  <div>
    <v-flex xs6 offset-xs3>
      <v-layout justify-center>
        <v-card flat max-width="400">
          <v-card-title>
            <h2 class="headline">Prolog</h2>
          </v-card-title>
          <v-card-text>{{ prolog }}</v-card-text>
          <v-card-text>{{ prolog2 }}</v-card-text>
          <v-card-actions>
            <v-btn flat color="orange" @click="nextStep">>> Weiter</v-btn>
          </v-card-actions>
        </v-card>
      </v-layout>
    </v-flex>
    <v-divider class="mt-5"></v-divider>
    <v-layout>
      <v-flex xs2 inline>
        <v-card-actions>
          <v-text-field label="Lösungswort:" :success="answered" clearable :rules="[validate]"></v-text-field>
        </v-card-actions>
      </v-flex>
      <v-flex xs2>
        <v-card-actions>
          <v-btn
            :disabled="!answered"
            flat
            color="orange"
            @click="toStep(chapter)"
            class="mt-3"
          >>> Zurück zum Kapitel</v-btn>
        </v-card-actions>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
export default {
  props: ["nextStep", "toStep"],
  data: () => ({
    answered: false,
    chapter: 0,
    prolog:
      'Eines Tages schlenderte ich über den Markt von Republika als ich auf einem Stand ein abgenutztes Buch entdeckte. Es war nicht die schlechte Art von abgenutzt, sondern so als hätte es schon viele Abenteuer mitgemacht. Ich schaute es mir genauer an: "Das Tagebuch von Walter dem Wasserbändiger"',
    prolog2:
      '"Walter der Wasserbändiger?" dachte ich. Das war doch dieser alte Kauz aus unserem Dorf. Er erzählte immer Geschichten von seinen Abenteuern und das er das größte Geheimnis von Republika entdeckt hatte. Aber die Leute glaubten ihm nicht und hielten ihn für verrückt. Immer wenn er anfing zu erzählen riefen Sie "Alter Walter! Nicht die Geschichte schon wieder!" und lachten. Vor ein paar Jahren verschwand er dann spurlos...'
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      switch (btoa(input)) {
        case "UkVMSUtURQ==":
          this.chapter = 2;
          this.answered = true;
          break;
        case "UFJJTlpFU1NJTg==":
          this.chapter = 3;
          this.answered = true;
          break;
        case "V0lSQkVMV0lORA==":
          this.chapter = 4;
          this.answered = true;
          break;
        case "U0NISUZG":
          this.chapter = 4;
          this.answered = true;
          break;
        case "U0VSRU5JVFk=":
          this.chapter = 5;
          this.answered = true;
          break;
        case "VEVNUEVM":
          this.chapter = 5;
          this.answered = true;
          break;
        case "VElBUkE=":
          this.chapter = 6;
          this.answered = true;
          break;
        case "WUFNQSBOTyBLQVpF":
          this.chapter = 7;
          this.answered = true;
          break;
        case "WUFNQV9OT19LQVp":
          this.chapter = 7;
          this.answered = true;
          break;
        case "S09MSUJSSQ==":
          this.chapter = 8;
          this.answered = true;
          break;
        case "TUFDSEVURQ==":
          this.chapter = 8;
          this.answered = true;
          break;
        case "QkxBVFRMQVVTTU9TS0lUTw==":
          this.chapter = 9;
          this.answered = true;
          break;
        case "V09MS0VOU1RBRFQ=":
          this.chapter = 9;
          this.answered = true;
          break;
        case "U1BSVU5HIElOUyBCTEFVRQ==":
          this.chapter = 10;
          this.answered = true;
          break;
        case "RFJBQ0hFTg==":
          this.chapter = 11;
          this.answered = true;
          break;
        case "U0VFTEVOQlJBTkQ=":
          this.chapter = 12;
          this.answered = true;
          break;
        case "QUxUQVIgREVSIEZFVUVSUklOR0U=":
          this.chapter = 13;
          this.answered = true;
          break;
        case "U09OTkVOUklORw==":
          this.chapter = 14;
          this.answered = true;
          break;
        case "S0lFU0VMQ0hFTg==":
          this.chapter = 15;
          this.answered = true;
          break;
        case "SElOS0VMU1RFSU4=":
          this.chapter = 15;
          this.answered = true;
          break;
        case "QUtFNExX":
          this.chapter = 16;
          this.answered = true;
          break;
        case "U0NIT0tPUklFR0VM":
          this.chapter = 17;
          this.answered = true;
          break;
        case "T0JTSURJQU4=":
          this.chapter = 18;
          this.answered = true;
          break;
        case "RVJEQsRORElHRU4=":
          this.chapter = 19;
          this.answered = true;
          break;
        case "V0VMVEVOQkFVTQ==":
          this.chapter = 20;
          this.answered = true;
          break;
        default:
          this.answered = false;
          break;
      }

      return this.answered;
    }
  }
};
</script>

