<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-text>{{ unterkapitel3 }}</v-card-text>
                        <v-card-text>
          <v-layout align-center column>
            <v-list>
              <v-list-tile style="height:35px" v-for="r in raetsel" :key="r">
                <i class="body-1">{{ r }}</i>
              </v-list-tile>
            </v-list>
          </v-layout>
        </v-card-text>
                <v-card-text>{{ unterkapitel4 }}</v-card-text>
        <v-card-actions>
          <v-text-field
            label="Lösungswort:"
            :success="answered"
            clearable
            :rules="[validate]"
          ></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from 'vue';

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Sand im Schuh",
    unterkapitel1: 'Nach 2 Tagen Wanderung durch verdorrtes Land erreichte ich den Hafen der Feuernation, buchte eine Überfahrt zur Erdnation und las am Hafen auf einer Bank in Walters Tagebuch. Shikari wusste von Walter. Aber der Sonnenring war an seinem Platz. Hatte Walter ihn nicht mitgenommen? In Gedanken drehte ich den Ring zwischen meinen Fingern. Er war warm und hatte einen runden Edelstein der das Licht reflektierte, als würden flammen in ihm züngeln. Ich blätterte umher und fand doch wieder nichts. Wenn ich ihn nur fragen könnte.',
    unterkapitel2: 'Ich ging aufs Schiff und wir legten ab. Auf dem Deck war es ruhig. Ich setzte mich auf eine Kiste nahe am Mast und las im Buch der Relikte. ',
    unterkapitel3: 'Das Erdkönigreich setzt auf unbeugsame Stärke. Organisation und Stolz zeichnet das Volk aus. Mehrere Seiten widmete das Buch der Großen Straße die quer durchs Land nach Kieselchen führte. Schöne Illustrationen zierten die Seiten. Für eine Abschrift eines Praktikanten war es bemerkenswert. Ein Rätsel gab es auch hier. ',
        raetsel: [
      "Riesel riesel kleiner Kiesel.",
      "Raunen tut sich auf am Hang.",
      "Bewegung. Wackeln. Leichtes Zittern.",
      "Unter deinen Füßen setzt es sich in Gang.",
      "Halt dich Oben, es geht bergab.",
      "Da ist die Klippe, es geht hinab.",
      "Schienen am Arm aus Obsidian.",
      "Hätten dich gerettet, deinen Stand gefestigt.",
      "Doch nun liegst du da und siehst durch ein Tor.",
      "Dort hängen die Steine. Kommt schon mal vor.",
    ],
    unterkapitel4: 'Diesmal war es nicht schwer. Ich legte mich auf meiner Kiste hin und döste bis ich in erholsamen Schlaf fiel. Mein letzter Gedanke war der Stadtname, der Stadt die mein nächstes Ziel war.',

  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "S0lFU0VMQ0hFTg==") {
        this.answered = true;
      } else {
        this.answered = false;
      }
      
      return this.answered
    }
  }
};
</script>

