    Chapter20
  },
  data: () => ({
    e1: 0,
    maxChapter: 20
  }),
  methods: {
    nextStep: function() {
      this.e1 = +this.e1;
      if (this.e1 < this.maxChapter) {
        this.e1 = this.e1 + 1;
      }
    },
    toStep: function(chapter) {
      if (chapter > 0 && chapter <= this.maxChapter) {
        this.e1 = chapter;
      }
    }
  }
};
