<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-actions>
          <v-text-field
            label="Lösungswort:"
            :success="answered"
            clearable
            :rules="[validate]"
          ></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from 'vue';

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Walters Geschichte",
    unterkapitel1: '"Dein Vater hat dich gut ausgebildet. Er war mir immer schon sympathisch. Du trägst interessante Gegenstände bei dir." Er guckte mich ernst an. "Weißt du was du tust?" Mit dieser Frage hatte ich nicht gerechnet. "Nein. Nicht wirklich. Aber es fühlt sich richtig an. Warum hast du es nicht gemacht? Du warst doch überall an den richtigen Orten." Walter Atmete einmal schwer durch. Ruhig antwortete er: "Es war mir nicht bestimmt. Ich hatte nie eins der Relikte in den Händen. Doch ich habe etwas anderes Gefunden." Er lehnte sich zurück und legte seinen ersten Stein. "Schon bei der Tiara des Mondes versagte ich. Ich habe Monate danach gesucht, aber nie den richtigen Hinweis gefunden. So ging ich weiter, auf dass ich zuerst ein anderes Relikt finden würde. Und so ging es dann auch mit den anderen 3 Relikten weiter. Am Ende gab ich auf." Er trank langsam einen Schluck Tee und fuhr fort. "Eines Nachts auf dem Rückweg zum Wasserstamm traf ich einen alten Wanderer. Er war in seinen Reisemantel gehüllt und sein Gesicht war unter einer weiten Kapuze verdeckt. Er sprach Leise und seine Stimme brannte im Kopf wie Eis und Feuer. Er sprach von einem Stein und zeigte immer wieder auf seine verhüllte Stirn. Er griff sich fest ins Gesicht und fing an vor Schmerzen zu schreien. Ich erschauderte und wollte fliehen, doch konnte mich nicht bewegen. Er riss seinen Arm von sich und der Mantel fiel unter Ächtsen und kreischen in sich zusammen. Zu Boden fiel ein rot glühender Stein. Er pulsierte auf dem Boden und schien nach mir zu rufen. Er weckte in mir ein brennendes Verlangen. ~Heb mich auf. Besitze mich. Nimm mich hoch. Guck tief in mich hinein. Lass mich in deinen Kopf.~ Immer wieder wiederholte sich das Flüstern in meinem Kopf. Mit all meiner Willenskraft packte ich den Mantel vom Boden und wickelte den Stein darin ein. Daraufhin ging ich weiter. Das flüstern war zwar leiser, aber es brachte mich um den Verstand. Ich weiß nicht mehr viel von dem was dann in den nächsten 5 Jahren passiert war. Ich weiß noch, dass ich wieder im Dorf war. Du warst auch da. Doch ich lebte in Träumen und erzählte nur noch Unsinn der für mich real war. Den Stein hatte ich auch noch. Dann lange Zeit gar nichts. Eines Tages Traf ich den Avatar Fentimon. Sie legte mir liebevoll die Hand auf die Stirn. Die Schmerzen verschwanden. Die Stimmen vergingen. Der Nebel lichtete sich. Ich war wieder ich. Fentimon schickte mich nach Republika und lies meine Seele vom weißen Lotus überwachen. Ich war dem obersten Übel entkommen, weiß ich heute." Er strich sich durch seinen hell weißen Bart und guckte mich durchdringend an. "An der Stelle kommen wir zu dir. Was du bis hier erlebt hast, war dein Schicksal. Daran habe ich keinen Zweifel.  Doch wo dein Schicksal endet wissen wir nicht und dein Schicksal könnte jedes Leben auf dieser Erde verbessern, oder verdammen. Der Stein der mich besaß war der schwarze Geisterstein. Seitdem der Avatar ihn aus meiner Stirn bändigte ist er verschwunden. Was auch immer das Geheimnis von Republika ist, es ist mit dem schwarzen Geisterstein verbunden. Ich erinnere mich, dass ich in meinem Wahn immer an einer Stelle am Stamm eines Baumes gekratzt habe. Er stand umgeben von Mauern Türen und Fenstern. Wo genau das war musst du selbst rausfinden. Wenn es wirklich Schicksal ist, weißt du jetzt schon wo du suchen musst. Was ich mit meinen rohen Fingern dort eingeritzt habe, wird dich ganz sicher weiterbringen. Ich hoffe nur, dass wir alle den morgigen Tag noch erleben, wenn du findest was du suchst." Walter sah sich das Pai Sho Spielfeld an, legte einen Stein und sagte "Du hast mich gewinnen lassen. Das ist nett." Er stand auf, verbeugte sich und verschwand so unauffällig in der Teestube, wie er aufgetaucht war.',
    unterkapitel2: 'Ich stand auf und verließ den Laden Richtung Marktplatz.',
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "V0VMVEVOQkFVTQ==") {
        this.answered = true;
      } else {
        this.answered = false;
      }
      
      return this.answered
    }
  }
};
</script>

