<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-text>{{ unterkapitel3 }}</v-card-text>
        <v-card-actions>
          <v-text-field label="Lösungswort:" :success="answered" clearable :rules="[validate]"></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from "vue";

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Steiniger Weg",
    unterkapitel1:
      '"Gut gemacht. Auch wenn die Tabelle für deinen Lösungsweg echt ungeordnet und mäßig formatiert sind, hast du es doch geschafft." sagte Scrayos, der immer noch sein versteinertes Grinsen im Gesicht hatte. "Testen wir sein Geschick" rief Kero ein, der zwischen den gut gekleideten Herrschaften aussah wie ein beliebiger Mönch mit großem Schlüsselbund. "Die Probe der tausend Türen also." Stimmte Scayos zu. Mir fielen in diesem Moment die nachtschwarzen Armschienen auf, die unter seinen Ärmeln einen kurzen Moment zu sehen waren. Sie waren komplett Matt und reflektierten kein Licht.',
    unterkapitel2:
      'Ich wurde in einen Raum gebracht und sah eine Tür vor mir. "Bis bald. Hier nimm das mit. Falls es mal wieder länger dauert" Die Wache die mich begleitete gab mir einen Schokoriegel und eine Trinkflasche mit.',
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "U0NIT0tPUklFR0VM") {
        this.answered = true;
      } else {
        this.answered = false;
      }

      return this.answered;
    }
  }
};
</script>

