<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-actions>
          <v-text-field
            label="Lösungswort:"
            :success="answered"
            clearable
            :rules="[validate]"
          ></v-text-field>
        </v-card-actions>
        <v-card-text v-if="answered">{{ unterkapitel3 }}</v-card-text>
        <v-card-actions v-if="answered">
          <v-text-field
            label="Lösungswort:"
            :success="answered2"
            clearable
            :rules="[validate2]"
          ></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered2" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from 'vue';

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    answered2: false,
    headline: "Kieselchen",
    unterkapitel1: '"Captain Rangox wir haben vertäut! …" Der erste Mat machte seine Meldungen und gab Befehle an die Crew. Hektik war an Deck ausgebrochen und weckte mich. Wir waren im Hafen der Erdnation angelangt und die Handelsgüter wurden gerade an Deck geholt. Ich verstaute meine Habe und machte mich auf den Weg. ',
    unterkapitel2: 'Raus aus der Hafenstadt, über Berg und Tal. Die Große Straße der Erdnation verband die Hafenstadt mit dem weit im Inland liegenden Kieselchen. Nie zuvor war ich auf einer so breiten und makellosen Straße unterwegs gewesen. So weit man sehen konnte, fand man auf dieser Straße weder ein Schlagloch, noch einen festgetretenen Kaugumfelsen(Kaugummi). Nach langem bewandern und bewundern der Straße kam ich in ein kleines Tal zwischen steilen Felswänden. Große Tore waren fein eingearbeitet in die Felswände. Eines der Tore stand offen und lies tief in eine hell erleuchtete Halle blicken. Ich lief durch das Tor und blickte mich um. Die Halle war zwar recht groß, aber bei weitem nicht so beeindruckend, wie die Händler auf dem Marktplatz immer erzählten. Ich ging weiter hinein. Breite Treppen führten immer tiefer ins Massiv und wurden mehrmals von weiteren Ebenen Unterbrochen. Menschen drängelten sich dicht an dicht auf den Treppen. Als ich die dritte Ebene betrat, blieb ich einen Moment im Gedränge stehen und reckte den Kopf staunend nach oben. Die ganze Zeit die ich nun auf den Treppen und in den kleinen Hallen auf dem Weg nach unten verbrachte, verbrachte ich nur im Eingangsbereich. Ich stand auf einer Plattform in einer hell erleuchteten Höhle. Überall um mich herum sah ich andere schwebende Plattformen, die nur durch Treppen und ranken des Bewuchses verbunden waren. Überall waren Händler, Musikanten, im Gras liegende Menschen. Ein Wasserfall fiel von Plattform zu Plattform und dann runter in ein Nichts, das tiefer schien, als ich je gesehen hatte. Plötzlich packte mich jemand hart an der Schulter und zog mich beiseite. "Hey was soll das?" fragte ich aufgebracht vor Schreck. "Entschuldige." Ein breites versteinertes Grinsen strahlte mich an. "Willkommen in den hängenden Gärten von Kieselchen. Wir haben dich erwartet." Sagte der grinsende junge Mann. Er trug eine locker sitzende Lederrüstung und eine Spitzhacke am Gürtel, die kunstvoll den Schriftzug "Mending is Love" auf dem Griff eingraviert hatte. Die Frage wozu man als Erdbändiger eine Spitzhacke brauchte verkniff ich mir lieber. "Wer bist du und was heißt ‘wir’ haben dich erwartet?" fragte ich noch immer etwas argwöhnisch. "Ich bin Scrayos." Er zog beiläufig eine Nagelschere aus der Tasche und knipste akribisch ein hervorstehendes Blatt an einem Busch ab. "Wo war ich? Ach ja. Wir sind die Mitglieder des Hinkelsteinbündnisses. Wir beobachten dich jetzt schon länger. Überall wo der Wind den Staub am Boden aufwirbelt, haben wir unsere Augen und Ohren. Aber wir wollen dir nichts Böses. Wir wollen dich dein Ziel erreichen sehen. Natürlich müssen wir dich vorher prüfen. Also bitte folge mir." Er drehte sich um und ging voraus. Ich war etwas überfordert, folge ihm aber, da ich nicht das Gefühl hatte eine Wahl zu haben.',
    unterkapitel3: 'Wir kamen auf eine Plattform, die weit oben schwebte und zu der nur eine gut bewachte Hängebrücke führte. An allen Seiten der Plattform lief Wasser hinunter, das aus den am Rand aufgestellten Blumentöpfen zu kommen schien. In der Mitte war ein großer Tisch der Kunstfertig aus einem einzigen Stein gehauen war. "Setz dich" sagte Scrayos und bot mir einen Steinhocker an. An dem Tisch saßen bereits einige wichtig aussehende Gesichter in feinster Kleidung und Kero. Scrayos stellte sich hinter seinen Stuhl und alle wandten sich ihm zu. "Willkommen zur Teambesprechung am Hinkelstein. Wir haben heute einen speziellen Gast. Er versucht das Geheimnis von Republika zu lösen. Dazu braucht er meine Armschienen. Wir sind hier um abzustimmen, ob er würdig ist die ‘Schienen des Unbändigen’ zu tragen." "Prüfen wir zuerst seinen Geist!" rief ein älterer Mann in die Runde. Alle klopften zustimmend auf den Tisch. "Eine Prüfung des Geistes also." fuhr Scrayos fort. "Gut. So sei es. Zuerst finde 6 mal 6 Zeichen in dieser Stadt. Dann sammle 6 Hinweise und bilde aus 36 Zeichen 6 richtige. Das Spiel heißt Mastersteind. Dann komm wieder zu uns und wir sagen dir ob du bestanden hast.'
    }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "SElOS0VMU1RFSU4=") {
        this.answered = true;
      } else {
        this.answered = false;
      }
      
      return this.answered
    },
    validate2: function(input) {
      if (input == undefined) {
        this.answered2 = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "QUtFNExX") {
        this.answered2 = true;
      } else {
        this.answered2 = false;
      }
      
      return this.answered2
    }
  }
};
</script>

<style>
</style>
