<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-actions>
          <v-text-field label="Lösungswort:" :success="answered" clearable :rules="[validate]"></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from "vue";

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Der Nether",
    unterkapitel1:
      'Sie willigte also ein und wir sprachen auf dem Weg zum Portal der Feuergeisterwelt über alles was uns in den Sinn kam. Ihr Name war Shikari Feuergast. Ihr Vater war ein Chiblocker und Ihre Mutter eine Feuerbändigerin. Sie reiste viel durch die Welt und erkundete Städte, schloss sich anderen Reisenden an und half Menschen wo sie nur konnte. Sie war groß, hatte Sommersprossen, kurze schwarze Haare und Wache grüne Augen. "Wir sind da. Das Portal zur Feuergeisterwelt. Oder einfach gesagt der Nether, Wir sehen uns drüben" Sie sah mich zögern und schubste mich in die wabernd lilane Wand. Mit nur einem blinzeln verschwand die grüne Welt an diesem schönen Sommertag und wich einem dunklen Raum. Rote Wände und Luft, die so heiß war, dass sie beim Einatmen brannte wie Feuer, umgaben mich nun. Wieder wurde ich geschubst. "Komm schon. Wer hier trödelt, trödelt nie wieder." Sie grinste vergnügt, packte meine Hand und zog mich hinter sich her. Meine Haut brannte am ganzen Körper. Schweiß verdampfte bevor er die Poren verließ. Warum war es hier so unglaublich heiß? Am Ende des Korridors öffnete sich eine riesige Höhle vor uns. Alles glühte orange. Dann begriff ich. Das glühen kam von einem Ozean aus Lava, der tief unter uns den Boden der Höhle ausfüllte.Wir gingen einen Schmalen gepflasterten Weg entlang. Ich blickte zurück. Mein Körper wollte umdrehen. Flucht. So Heiß. Alles Brennt... "Hey du schaffst das schon. Wir müssen nicht weit. Entfernungen sind hier schnell zu überwinden." Shikari legte mir den Arm um die Hüfte. Meine Angst verflog und ich nahm einen tiefen schmerzhaften Atemzug. Ich sah einen schmalen Pfad, der hoch über den Ozean aus Lava führte. "Bleib dicht bei mir." Sie ging vor und zog mich sanft hinterher. Der Pfad hatte immer mehr Lücken. "Spring mir hinterher. Du schaffst das! Geübt und nahezu mühelos sprang sie von einem Vorsprung zum anderen und ich folgte ihr.',
    }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "U0VFTEVOQlJBTkQ=") {
        this.answered = true;
      } else {
        this.answered = false;
      }

      return this.answered;
    }
  }
};
</script>

