<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-actions>
          <v-text-field label="Lösungswort:" :success="answered" clearable :rules="[validate]"></v-text-field>
        </v-card-actions>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from "vue";

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Yama no Kaze",
    unterkapitel1:
      "Auf dem Weg in die Stadt fühlte ich die Ruhe die diese Region ausstrahlte. Es war als würde die Seele Urlaub machen. Ich vergas den Schmerz in meinen Füßen und die Anstrengungen, die ich für mein Abendteuer auf mich nahm. Als ich am Ende eines Tunnels ankam, traute ich meinen Augen nicht. Da schwebte wirklich eine ganze Stadt am Himmel. Sie war anscheinend gerade dabei sich dem Anleger am Berg zu nähern. So ein Glück wusste ich. Nach allem was ich darüber gehört hatte, ist die Stadt wohl nur 2 von 14 Tagen erreichbar. Den Rest der Zeit war sie wohl in anderen Teilen der Welt unterwegs.",
    unterkapitel2:
      'Als ich an den Toren der Stadt angekommen war, flog sogar ein Bison in einiger Entfernung vorbei. In der Stadt glitten Kinder in einem Spiel umher. “Hey Vorsicht!” Eine Hand packte mich an der Schulter und zog mich zurück, während vor mir auch schon eins der Kinder entlangzischte. “Geh nicht aufs Spielfeld. Entschuldige. Hallo, ich bin Kero. Du bist nicht von hier. Ein Wasserbändiger? Was bringt dich zu uns?” Ich erzählte ihm von meiner Reise und er guckte mich prüfend an. “Das ist kann ein gefährliches Unterfangen sein. Aber wenn du schon die Tiara des Mondes hast, traue ich dir das zu. Hier bei uns in der Stadt gibt es eine verschlossene Tür, die nur aufgeht, wenn man sich würdig erweist. Erkunde die Stadt nach belieben und löse das Rätsel. Ich öffne dir jede Tür. Du suchst den "Schal der Wolken".'
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "S09MSUJSSQ==") {
        this.answered = true;
      } else {
        this.answered = false;
      }

      return this.answered;
    }
  }
};
</script>

